.block {
  height: $block-height;

  display: grid;
  place-content: center;

  $colors:
    1 rgb(255, 0, 0),
    2 rgb(204, 0, 0),
    3 rgb(153, 0, 0),
    4 rgb(102, 0, 0),
    5 rgb(51, 0, 0),
    6 rgb(0, 0, 0);

  @each $number, $color in $colors {
    &--#{$number} {
      background-color: $color;
    }
  }
}
